<template>
  <div class="page page-roadshow-list">
    <div class="list-options">
      <!--  :labelCol="{ span: 10 }" :wrapperCol="{ span: 10 }"  -->
      <div class="flex items-center">
        <div class="flex items-center">
          <span>关键字：</span>
          <a-input size="small" v-model="params.keyword" style="width: 200px" placeholder="角色/权限名称" />
        </div>
        <a-button style="margin-left: 50px" type="primary" icon="search" size="small" :loading="loading" @click="handleSearch">查询</a-button>
      </div>
    </div>
    <a-divider></a-divider>
    <a-space class="v-mb-md">
      <a-button type="primary" size="small" style="min-width: 60px" @click="handleAdd">新增</a-button>
      <a-button type="primary" size="small" style="min-width: 60px" @click="handleDelBatch">删除</a-button>
    </a-space>
    <div class="list-container">
      <a-table
        :columns="columns"
        :dataSource="list"
        :pagination="{
          showQuickJumper: true,
          showSizeChanger: true,
          hideOnSinglePage: false,
          pageSizeOptions: ['10', '20', '30', '50'],
          total: pagination.totalRow,
          pageSize: pagination.pageSize,
          current: pagination.pageNumber,
          onChange: onPageChange,
          onShowSizeChange: onPageChange,
        }"
        :rowSelection="{ selectedRowKeys: selectedRowKeys, onChange: onSelectChange }"
        :scroll="{ x: '100%' }"
        :loading="loading"
        tableLayout="auto"
        class="text-no-wrap"
        rowKey="id"
        size="small"
      >
        <template slot="action" slot-scope="text, record">
          <!-- <a-popconfirm @confirm="updateDoc(record)" cancelText="取消" okText="确认" placement="top">
            <template slot="title">
              <div v-html="record.status === 0 ? '确认冻结 ?' : '确认解冻 ?'"></div>
            </template>
            <a-icon slot="icon" style="color: red" type="info-circle" />
            <a-button class="text-indigo" :icon="record.status === 0 ? 'eye' : 'eye-invisible'" size="small" :title="record.status === 0 ? '发布' : '取消发布'" type="link"></a-button>
          </a-popconfirm> -->
          <a-button class="text-red" icon="audit" size="small" title="授权" type="link" @click="handleAuth(record)"></a-button>
          <a-button class="text-red" icon="edit" size="small" title="编辑" type="link" @click="handleEdit(record)"></a-button>
          <a-popconfirm placement="top" okText="确认" cancelText="取消" @confirm="handleDel(record)">
            <template slot="title">
              <div>确认删除 ?</div>
            </template>
            <a-icon slot="icon" type="info-circle" style="color: red" />
            <a-button class="text-red" icon="close" size="small" title="删除" type="link"></a-button>
          </a-popconfirm>
        </template>
      </a-table>
    </div>
    <!-- 编辑/新增角色 -->
    <a-modal :confirmLoading="modalSaving" :visible="modalVisible" :width="400" @cancel="modalVisible = false" @ok="handleSave" centered okText="保存" :title="modalTitle">
      <a-form-model ref="ruleForm" :model="form" :rules="formRules" :label-col="{ span: 6 }" :wrapper-col="{ span: 18 }">
        <a-form-model-item label="角色名称" prop="name">
          <a-input v-model="form.name" placeholder="输入名称" />
        </a-form-model-item>
        <a-form-model-item label="角色编号" prop="code">
          <a-input v-model="form.code" placeholder="输入编号" />
        </a-form-model-item>
      </a-form-model>
    </a-modal>

    <!-- :selected-keys="selectedKeys" -->
    <a-modal :confirmLoading="authSaving" :visible="authModal" :width="600" @cancel="authModal = false" @ok="handleSaveAuth" centered okText="保存" title="设置权限">
      <a-transfer
        show-search
        :data-source="mockData"
        :titles="['未设置的权限', '已设置权限']"
        :target-keys="targetKeys"
        :render="(item) => item.title"
        :filter-option="filterAuthOption"
        @change="handleAuthChange"
        @selectChange="handleAuthSelectChange"
        @scroll="handleScroll"
        :list-style="{ height: '400px', width: '255px' }"
      />
    </a-modal>
  </div>
</template>
<script>
const form = {
  id: undefined,
  name: '',
  code: '',
}
export default {
  data() {
    const mockData = []
    for (let i = 0; i < 20; i++) {
      mockData.push({
        key: i.toString(),
        title: `权限名称${i + 1}`,
        description: `description of content${i + 1}`,
        disabled: i % 3 < 1,
      })
    }
    const oriTargetKeys = mockData.filter((item) => +item.key % 3 > 1).map((item) => item.key)
    return {
      mockData,
      targetKeys: oriTargetKeys,
      // selectedKeys: ['1', '4'],
      form: { ...form },
      formRules: {
        name: [{ required: true, message: '请输入角色名称', trigger: 'blur' }],
        code: [{ required: true, message: '请输入角色编码', trigger: 'blur' }],
      },
      modalTitle: '新增角色',
      modalVisible: false,
      modalSaving: false,
      authSaving: false,
      authModal: false,
      loading: false,
      list: [],
      selectedRowKeys: [],
      selectedRows: [],
      pagination: {
        totalPage: 1,
        pageNumber: 1,
        pageSize: 10,
        totalRow: 0,
      },
      params: {
        keyword: '',
        status: undefined,
      },
      columns: [
        {
          title: '序号',
          dataIndex: 'id',
          width: 50,
          align: 'center',
          customRender: (text, record, index) => index + 1,
        },
        {
          title: '角色名称',
          dataIndex: 'name',
          align: 'left',
        },
        {
          title: '编号',
          dataIndex: 'code',
        },
        {
          title: '状态',
          dataIndex: 'status',
          // width: 100,
        },
        {
          title: '类型',
          dataIndex: 'type',
          // width: 100,
        },
        {
          title: '创建时间',
          dataIndex: 'createTime',
          align: 'center',
          width: 122,
        },
        {
          title: '修改时间',
          dataIndex: 'updateTime',
          align: 'center',
          width: 122,
        },
        {
          title: '操作',
          dataIndex: 'action',
          width: 100,
          fixed: 'right',
          align: 'center',
          scopedSlots: { customRender: 'action' },
        },
      ],
    }
  },
  computed: {
    user: (vm) => vm.$store.state.user.user,
  },
  methods: {
    handleSearch(e) {
      this.load(1, 10)
    },
    onStatusChange() {},
    load(pageNumber = 1, pageSize = 10) {
      this.loading = true
      setTimeout(() => {
        let list = []
        for (let index = 0; index < pageSize; index++) {
          let idx = (pageNumber - 1) * pageSize + index
          list.push({
            id: idx,
            name: `角色名称${index}`,
            createTime: '2021-04-21 10:' + (60 - index),
            updateTime: '2021-04-21 14:' + (60 - index),
            code: 'XCSDDGDS',
            status: 'XCSDDGDS',
            type: 'JREGRG',
          })
        }
        let totalRow = 24
        this.pagination = {
          // firstPage,
          // lastPage,
          pageNumber: pageNumber,
          pageSize,
          // totalPage,
          totalRow,
        }
        this.list = list
        this.loading = false
      }, 500)
    },
    onPageChange(pageNumber, pageSize) {
      this.load(pageNumber, pageSize)
    },
    onPageSizeChange(pageNumber, pageSize) {
      this.load(pageNumber, pageSize)
    },
    onSelectChange(selectedRowKeys, selectedRows) {
      this.selectedRowKeys = selectedRowKeys
      this.selectedRows = [...selectedRows]
    },
    handleDel(record) {
      console.log(record)
    },
    handleDelBatch() {
      let vm = this
      let selectedRowKeys = vm.selectedRowKeys
      if (selectedRowKeys.length === 0) {
        this.$message.warning('请选择至少一条记录')
        return
      }
      this.$confirm({
        title: '确认删除所选择的记录？',
        cancelText: '',
        icon: 'info-circle',
        onOk() {
          let { pageNumber, pageSize } = vm.pagination
          vm.load(pageNumber, pageSize)
        },
      })
    },
    handleAdd() {
      this.form = { ...form }
      this.modalTitle = '新增角色'
      this.modalVisible = true
    },
    handleEdit(record) {
      this.form = { ...record }
      this.modalTitle = '编辑角色'
      this.modalVisible = true
    },
    handleSave() {
      this.$refs.ruleForm.validate((valid) => {
        if (valid) {
          this.modalSaving = true
          setTimeout(() => {
            this.modalSaving = false
            this.modalVisible = false
            let { pageNumber, pageSize } = this.pagination
            this.load(pageNumber, pageSize)
          }, 500)
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    filterAuthOption(inputValue, option) {
      return option.description.indexOf(inputValue) > -1
    },
    handleSaveAuth() {},
    handleAuthChange(targetKeys, direction, moveKeys) {
      console.log(targetKeys, direction, moveKeys)
      this.targetKeys = targetKeys
    },
    handleAuthSelectChange() {},
    handleAuth(record) {
      console.log(record)
      this.authModal = true
    },
  },
  mounted() {
    let { pageNumber, pageSize } = this.pagination
    this.load(pageNumber, pageSize)
  },
}
</script>
